<template>
	<div class="image-display">
		<div class="image-container">
			<img :src="imageUrl" :style="{width: '100%'}" />
			<u-alert :description="text" type="primary"
				customStyle="margin-top: 10px;background-color: #ecf5ff3b;border-radius: 30rpx;" fontSize=13></u-alert>
		</div>
		<view class="dingwei">
			<u-row>
				<u-col span="3">
					<view class="demo-layout">
						<u-icon label="返回" @click="Back" labelSize="13" labelColor="#fff" size="20"
							name="https://shopimges.oss-cn-hangzhou.aliyuncs.com/wallpaper/fh.png" labelPos="bottom"
							space="5"></u-icon>
					</view>
				</u-col>
				<u-col span="3">
					<view class="demo-layout">
						<u-icon label="点赞" @click="showToast" labelSize="13" labelColor="#fff" size="20"
							name="https://shopimges.oss-cn-hangzhou.aliyuncs.com/wallpaper/dz.png" labelPos="bottom"
							space="5"></u-icon>
					</view>
				</u-col>
				<u-col span="3">
					<view class="demo-layout">
						<u-icon label="下载" @click="opdownloadFile" labelSize="13" labelColor="#fff" size="20"
							name="https://shopimges.oss-cn-hangzhou.aliyuncs.com/wallpaper/xz.png" labelPos="bottom"
							space="5"></u-icon>
					</view>
				</u-col>
				<u-col span="3">
					<view class="demo-layout">
						<button data-name="shareBtn" open-type="share">
							<u-icon label="分享" @click="onShareAppMessage" labelSize="13" labelColor="#fff" size="20"
								name="https://shopimges.oss-cn-hangzhou.aliyuncs.com/wallpaper/fx.png" labelPos="bottom"
								space="5"></u-icon>
						</button>
					</view>
				</u-col>
			</u-row>
		</view>
		<u-toast ref="uToast"></u-toast>
	</div>
</template>

<script>
	import uText from '../../uni_modules/uview-ui/components/u--text/u--text.vue';
	export default {
		components: {
			uText
		},
		data() {
			return {
				baseurl: "https://scene.liuyufeng.fun/app",
				obj: null,
				text: '', // 用于存储传入的图片名称
				imageUrl: '', // 用于存储传入的图片链接
				code: '',
				imgList: [],
			};
		},
		onLoad(e) {
			// 通过路由参数或者其他方式获取前部页面传入的图片链接
			console.log(e)
			this.obj = e;
			uni.showShareMenu({
				withShareTicket: true,
				menus: ["shareAppMessage", "shareTimeline"]
			})
			this.imageUrl = e.preview_url;
			this.text = e.name_zh;
		},
		methods: {
			onShareAppMessage() {
				return {
					title: '沄樊-世界风景图鉴',
					path: '/pages/home/ImageDisplay?id=' + this.obj.id +
						'&preview=' + this.obj.preview +
						'&code=' + this.obj.code +
						'&preview_url=' + this.obj.preview_url +
						'&name_zh=' + this.obj.name_zh +
						'&country_zh=' + this.obj.country_zh,
					imageUrl: this.obj.preview_url
				}
			},
			Back() {
				if (getCurrentPages().length > 1) {
					uni.navigateBack({
						delta: 1,
					})
				} else {
					uni.redirectTo({
						url: '/pages/home/index'
					})
				}
			},
			showToast() {
				let that = this;
				this.$refs.uToast.show({
					...{
						type: 'success',
						title: '点赞成功',
						message: "点赞成功",
						iconUrl: 'https://cdn.uviewui.com/uview/demo/toast/success.png'
					},
					complete() {}
				})
			},
			opdownloadFile() {
				var that = this
				uni.showLoading({
					title: '正在保存图片'
				});
				// 获取图片信息
				var url = that.baseurl + "/photo/fp_hd?code=" + that.obj.code + "&preview=" + that.obj.preview
				console.log(url)
				uni.downloadFile({
					url: url,
					success: (res) => {
						var benUrl = res.tempFilePath
						console.log(benUrl)
						//图片保存到本地相册
						uni.saveImageToPhotosAlbum({
							filePath: benUrl,
							//授权成功，保存图片
							success: function(data) {
								uni.hideLoading()
								uni.showModal({
									title: '温馨提示',
									content: '保存相册成功',
									showCancel: false,
									success(res) {
										if (res.confirm) {

										}
									}
								});
							},
							//授权失败
							fail: function(err) {
								if (err.errMsg) {
									console.log(err.errMsg)
									//重新授权弹框确认
									uni.showModal({
										title: '提示',
										content: '无权限，请打开下载权限后再试！',
										showCancel: false,
										success(res) {
											if (res.confirm) {
												//重新授权弹框用户点击了确定
												uni.openSetting({
													//进入小程序授权设置页面
													success(settingdata) {
														if (settingdata
															.authSetting[
																'scope.writePhotosAlbum'
															]) {
															//用户打开了保存图片授权开关
															uni.saveImageToPhotosAlbum({
																filePath: benUrl,
																success: function(
																	data
																) {
																	uni.hideLoading()
																	that.adShow =
																		true
																}
															});
														} else {
															//用户未打开保存图片到相册的授权开关
															uni.showModal({
																title: '温馨提示',
																content: '授权失败，请稍后重新获取',
																showCancel: false
															});
														}
													}
												});
											}
										}
									});
								}
								uni.hideLoading()
							}
						});
					},
					fail: (error) => {
						console.log(error)
						uni.hideLoading()
					}
				})
			}
		}
	};
</script>

<style scoped>
	.image-display {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		/* max-width: 100%; */
		height: 100vh;
		background-color: black;
	}

	.image-container {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
	}

	.image {
		max-width: 100%;
		max-height: 100%;
		cursor: pointer;
	}

	.dingwei {
		position: fixed;
		bottom: 5vh;
		left: 5vw;
		width: 80vw;
		background-color: #0b0f12;
		opacity: 0.6;
		padding: 10rpx 30rpx;
		border-radius: 50rpx;
		text-align: center;
	}

	button {
		border: none;
		background-color: transparent;
		outline: none;
	}
</style>